.markdown-body {
  position: relative;
  padding: 20px;
  margin: 0.5em 0;

  overflow-y: scroll;
  overscroll-behavior: contain;
  flex: 1;
}

.overlay {
  position: fixed;
  left: 0;
  top: 0;
  bottom: 0;
  right: 0;
  background-color: black;
  opacity: 0.3;

  z-index: 10;
}

.modal-wrapper {
  position: fixed;
  z-index: 15;
  text-align: initial;
  top: 20px;
  left: max(20px, calc((100vw - 980px) / 2));
  right: max(20px, calc((100vw - 980px) / 2));
  bottom: 20px;
  max-width: 980px;
  min-width: 320px;
  border-radius: 10px;
  background-color: #fff;
  border: 1px solid hsla(0, 0%, 0%, 30%);
  box-shadow: 0px 10px 13px -7px #000000, 5px 5px 15px 5px rgba(0, 0, 0, 0);
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Helvetica, Arial,
    sans-serif, "Apple Color Emoji", "Segoe UI Emoji";
  overflow: hidden;
  cursor: default;
}

.content-wrapper {
  position: relative;
  display: flex;
  height: 100%;
  flex-direction: column;
}

.content-wrapper .gh-emoji {
  width: 1em;
  vertical-align: middle;
}

.content-wrapper .modal-header {
  font-size: 1.2em;
  padding: 0.3em 0.6em;
  text-align: left;
  background-color: rgb(122, 124, 255);
}

.content-wrapper .modal-header a {
  text-decoration: none;
  color: white;
}

.content-wrapper .modal-header .close-button {
  position: absolute;
  right: 8px;
  top: 8px;
  vertical-align: middle;
  cursor: pointer;
}

@media (max-width: 767px) {
  .modal-wrapper {
    inset: 10px;
  }
}
